/*
*STYLE FOR FRONT PAGE
*/
//ABOUT SECTION
.entry-row-img{
	background-image: url(http://localhost/kasiaikod/wp-content/uploads/2018/02/Kasia_Kate_Jeziorska_blackwhite_ps-768x1024.jpg);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: right;
	background-size:contain;
	height: 800px;
  }
  .entry-row-second-img{
	background-image:url(http://localhost/kasiaikod/wp-content/uploads/2018/02/code_laptop_Kasia-i-kod-1024x684-1024x684.jpg);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: left;
	background-size:cover;
	height: 600px;
  }
//FRONT SEARCH FORM
.front-search-form {
	margin: 0 auto;
	max-width: 60rem;
	background: #efefef;
	padding: 40px 10%;
	text-align: center;
	display: flex;
	flex-wrap: wrap;
	flex: 0 0 100%;
	justify-content: center;
	align-items: center;
	.cat-list{
		display:flex;
		flex-direction: column;
		@include tablet{
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		}
		h4 {
			font-size: 0.7rem;
			background: #9d2160;
			text-align: center;
			color: #fff;
			font-size: 1em;
			// display: flex;
			// justify-content: center;
			// align-items: center;
			margin: 0 1% 20px;
			padding: 10px 20px;
			text-transform: uppercase;
		}
	}

}


//POSTS GRID
.post-ctn{
	margin: 0 auto;
	max-width: 60rem;
	@include tablet {
		grid-gap: 1rem;
		display: grid;
		grid-template-columns: 40% 40%;
	}
	@include desktop {
		grid-gap: 1rem;
		display: grid;
		grid-template-columns: 30% 30% 30%;
	}
}
